import React from 'react';
import Button from 'react-bootstrap/Button';
import axios from 'axios';
import swal from 'sweetalert';
import { useSelector, useDispatch } from 'react-redux';

function Password() {
  const changeState = useDispatch();

  //var html = 'https://priority-server.herokuapp.com';
  var html = '//localhost:3000';
  const viewPassword = useSelector(state => state.password);

  const loginLogin = useSelector(state => state.userlogin);
  const loginUsuario = useSelector(state => state.username);
  const loginCti = useSelector(state => state.cti);
  var senha = '';

  // função que busca um registro de login válido no servidor.
  const updateSenha = () => {
    senha = document.getElementById('inputSenha').value;
  };

  const sendData = () => {
    
    if (senha === '') {
      swal({
        title: 'ERRO',
        text: 'Por favor, insira uma senha!',
        icon: 'warning',
        dangerMode: true
      });
    } else {
      var obj = {
        user: loginLogin,
        password: senha
      };
      axios.post(html + "/updatepassword/'" + loginLogin + "'", obj);
      swal('SUCESSO', 'Senha alterada com sucesso!', 'success');
      changeState({
        type: 'PACIENTES_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadCti: loginCti
      });
    }
  };

  // eslint-disable-next-line
  if (viewPassword === 1) {
    return (
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          height: '70vh'
        }}
      >
        <label
          style={{
            fontWeight: 'bold',
            fontSize: 20,
            textAlign: 'center',
            marginTop: 20,
            marginBottom: 10
          }}
        >
          ALTERAR SENHA
        </label>
        <label
          style={{
            fontWeight: 'bold',
            fontSize: 24,
            color: 'red',
            textAlign: 'center',
            marginTop: 10,
            marginBottom: 10
          }}
        >
          USUÁRIO: {loginUsuario}
        </label>
        <label style={{ marginTop: 20, marginBottom: 10, fontSize: 16 }}>
          POR FAVOR, INSIRA A NOVA SENHA:
        </label>
        <input
          onChange={() => updateSenha()}
          autoComplete="off"
          style={{
            width: 200,
            height: 50,
            textAlign: 'center',
            borderRadius: 5,
            border: 'none',
            outline: 'none',
            backgroundColor: '#E5E7E9',
            transition: '0.3s all'
          }}
          placeholder="NOVA SENHA..."
          type="password"
          id="inputSenha"
        ></input>
        <h3 style={{ marginTop: 10, marginBottom: 20, fontSize: 12 }}>
          PROCURE USAR PELO MENOS 6 CARACTERES, INCLUINDO NÚMEROS, LETRAS
          MAIÚSCULAS E MINÚSCULAS.
        </h3>
        <Button
          onClick={() => sendData()}
          variant="success"
          style={{
            marginLeft: 5,
            marginTop: 20,
            marginBottom: 5
          }}
        >
          OK
        </Button>
      </div>
    );
  } else {
    return null;
  }
}
export default Password;
